.ChatLobby {
    width: 100%;
    height: 100vh;
}

.SetID {
    width: 100%;
    max-width: 380px;
    margin: 80px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
}
.SetID input {
    text-align: center;
    width: 100%;
    max-width: 280px;
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: 25px;
    margin: 10px 0;
}
.SetID button {
    text-align: center;
    font-weight: bold;
    background-color: #FADA5C;
    width: 100%;
    max-width: 280px;
    padding: 10px 20px;
    box-sizing: border-box;
    border-radius: 25px;
    margin: 10px 0;
}
.ChatRoom {
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
}
.ChatRoom .ChatLogs {
    
    flex: 1;
    /* padding: 10px;
    overflow-y: auto; */
}
.ChatRoom .MouseAIImg {
    width: 80px;
    height: 80px;
    display: flex;
    flex-direction: column;
    border-radius: 50%;
    margin-right: 20px;
}
.ChatRoom .ChatArea {
    display: flex;
    width: 100%;
    padding: 20px;
    flex-direction: row;
    align-items: center;
    box-sizing: border-box;
}
.ChatRoom .UserChatArea {
    display: flex;
    width: 100%;
    padding: 20px;
    flex-direction: row;
    align-items: center;
    justify-content: flex-end;
    box-sizing: border-box;
}
.ChatRoom .Chat {
    width: 80px;
    height: 80px;
    display: flex;
    flex-direction: column;
    border-radius: 50%;
}
.ChatRoom .ChatMessage {
    padding: 15px 25px;
    border-right: 1px solid #000;
    background-color: #fff;
    border-radius: 25px;
}
.ChatRoomHeader {
    padding: 15px;
    text-align: center;
    background-color: #FADA5C;
    font-weight: bold;
}
.ChatRoomInputArea {
    display: flex;
    padding: 10px;
    border-top: 1px solid #ddd;
}
.ChatRoomInputArea input {
    flex: 1;
    padding: 10px;
    border: none;
    border-radius: 5px;
    margin-right: 10px;
}
.ChatRoomInputArea button {
    padding: 10px 20px;
    background-color: #FADA5C;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}
